import util from '@/libs/util.ice'; // 

import Index from './pages/Index';
import Login from './pages/Login';
import Error404 from './pages/Error404';
import Demo1 from './pages/Demo1';
import Demo2 from './pages/Demo2';
import Page6 from './pages/page6';
import Page6One from './pages/page6-1';
import Page7 from './pages/page7';
import HeaderAside from './layouts/HeaderAside'; //  routerConfig  iceworks 
// ice 
// 
//  ice 
// {
//   path: '/page4',
//   layout: d2LayoutMain,
//   component: 4
// }
//  name  path  name = page-demo1
//  util.recursiveRouterConfig  path2name 
// meta  Object.assign 
//  meta.name name  name 
//  

const routerConfig = [
  //   name:index
  {
    path: '/',
    name: 'index',
    layout: HeaderAside,
    component: Index,
  }, //  
  {
    path: '/refresh',
    name: 'refresh',
    layout: HeaderAside,
    hidden: true,
    component: {
      beforeRouteEnter(to, from, next) {
        next(vm => vm.$router.replace(from.fullPath));
      },

      render: h => h(),
    },
  }, //  
  {
    path: '/redirect/:route*',
    name: 'redirect',
    layout: HeaderAside,
    hidden: true,
    component: {
      beforeRouteEnter(to, from, next) {
        next(vm => vm.$router.replace(JSON.parse(from.params.route)));
      },

      render: h => h(),
    },
  },
  {
    path: '/demo1',
    name: 'demo1',
    layout: HeaderAside,
    component: Demo1,
    meta: {
      requiresAuth: true,//
      title: ' 1',
    },
  },
  {
    path: '/demo2',
    layout: HeaderAside,
    component: Demo2,
    meta: {
      requiresAuth: true,
      title: ' 222',
    },
  },
  {
    path: '/page6',
    layout: HeaderAside,
    component: Page6,
  },
  {
    path: '/page6-1',
    layout: HeaderAside,
    component: Page6One,
  },
  {
    path: '/page7',
    layout: HeaderAside,
    component: Page7,
  },
]; // 
// ice 
// 
//  routerConfig

const routerConfigMenuOut = [
  // 
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {
      requiresAuth: false,
    },
  }, // 404
  {
    path: '*',
    component: Error404,
  },
]; // 
//  router.js 

export default util.recursiveRouterConfig([
  ...routerConfig,
  ...routerConfigMenuOut,
]); // 
//  mian.js 

export const frameInRoutes = util.recursiveRouterConfig(routerConfig).map(e => {
  const route = e.children ? e.children[0] : e;
  return {
    path: e.path,
    name: route.name,
    hidden: route.hidden,
    meta: route.meta,
  };
});
