import axios from '../node_modules/axios';

//
axios.defaults.baseURL='http://192.168.100.166:8081';

//promise.then()
/**
 * get
 * @param url
 * @param data
 * @returns {Promise}
 */

export function fetch(url,params={}){
    return new Promise((resolve,reject) => {
      axios.get(url,{
        params:params
      })
      .then(response => {
        resolve(response.data);
      })
      .catch(err => {
        reject(err);
        console.log('get')
      })
    })
  }
  
  
  /**
   * post
   * @param url
   * @param data
   * @returns {Promise}
   */
  
   export function post(url,data = {}){
     return new Promise((resolve,reject) => {
       axios.post(url,data)
            .then(response => {
              resolve(response.data);
            },err => {
              reject(err)
              console.log('post')
            })
     })
   }
  
